/**
  (CSS-in-CSS) https://nextjs.org/docs/app/building-your-application/styling/css-modules
  global.css 는 global-scope 로 동작 & 네이밍 충돌로 인한 collision 발생
 */

* {
    box-sizing: border-box;
}

html, body {
    padding: 0;
    margin: 0;
    background-color: #edf0f5;
}

main {
    min-height: auto;
    padding: 1rem 0;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

a {
    color: inherit;
    text-decoration: none;
}

/* iframe > no-scroll */
.no-scroll {
    overflow: hidden;
}

.no-scroll::-webkit-scrollbar {
    display: none;
}

.no-scroll {
    -ms-overflow-style: none; /* 인터넷 익스플로러 */
    scrollbar-width: none; /* 파이어폭스 */
}

/* Custom styles for the SplitPane divider */
.SplitPane .Resizer {
    background: url('public/static/dot.svg') center center no-repeat;
    background-size: contain;
    width: 7px; /* Adjust the width according to the size of your SVG */
    cursor: col-resize;
    z-index: 1;
    box-sizing: border-box;
    background-clip: padding-box;
}
